[dir] body {
    background-color: #f4f6f8;
}

.app-wrapper {
    font-family: Roboto;
}

.dashboard-header-bar {
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 1000;
}

[dir=ltr] .dashboard-header-bar {
    left: 0;
}

[dir=rtl] .dashboard-header-bar {
    right: 0;
}

[dir] .dashboard-wrapper {
    background-color: #f4f6f8;
}

[dir=ltr] .dashboard-wrapper {
    padding-left: 10px;
    padding-right: 10px;
}

[dir=rtl] .dashboard-wrapper {
    padding-right: 10px;
    padding-left: 10px;
}

@media screen and (max-width: 800px) {
    .titlebar-name {
        width: 100%;
    }
    [dir] .titlebar-name {
        text-align: center;
    }
    .react-grid-item {
        width: 100% !important;
        height: 390px !important;
        position: relative !important;
    }
    [dir] .react-grid-item {
        -webkit-transform: none !important;
            -ms-transform: none !important;
                transform: none !important;
        margin: 0 auto 10px auto !important;
    }
}

/* plugin TODO */

table.pivot * {
    -webkit-user-select: none !important;
    -moz-user-select: none !important;
    -ms-user-select: none !important;
    -o-user-select: none !important;
    user-select: none !important;
}

/* temporary workaround for headerbar */
[dir] .secondary-light-bg {
    background-color: #48a999;
}



